.booking-page {
    min-height: 100vh;
    .booking-header {
        padding: 24px;
    }

    .booking-header,
    .confirmation-header,
    .stay-listing-card,
    .cancellation,
    .ground-rules {
        position: relative;
        &::after {
            @include afterSeperator;
        }
    }

    @include for-narrow-layout {
        .air-cover,
        .reservation-details {
            position: relative;
            &::after {
                @include afterSeperator;
            }
        }
    }

    .confirmation-header {
        background-color: white;
        display: flex;
        justify-content: center;
        padding-block: 16px;
        position: sticky;
        top: 0;
        z-index: 3;
        .btn-go-back {
            position: absolute;
            left: 20px;
            top: 20px;
        }

        h2 {
            font-family: $font-family-bold;
            font-size: 16px;
        }

        @include for-narrow-layout {
            padding-block: 64px 16px;
            justify-content: start;
            z-index: 0;
            position: static;
            line-height: 1.125;
            &::after {
                content: none;
            }

            h2 {
                font-family: $font-family-medium;
                font-size: 32px;
                margin-left: 32px;
            }

            .btn-go-back {
                position: static;
            }
        }
    }

    // .mobile-stay-booking,
    // .desktop-stay-booking {
    //     min-height: 100vh;
    // }

    .air-cover {
        display: flex;
        align-items: baseline;
        flex-wrap: wrap;
        font-size: 14px;
        padding-block: 24px;
        img {
            height: 11px;
            padding-left: 2px;
        }
        @include for-narrow-layout {
            font-size: 16px;
            img {
                height: 13px;
            }
        }
    }

    .price-details {
        padding-block: 24px;
        font-size: 16px;
        h4 {
            font-size: 22px;
            font-family: $font-family-medium;
        }
    }

    .login-signup {
        margin-top: 24px;
    }

    .app-footer {
        padding-block: 24px;
        position: relative;
        background-color: rgb(235, 235, 235);
        align-items: center;
    }
}
